.container {
    display: flex;
    justify-content: center;
    padding: 0;
    margin-top: 8rem;
}

.container img {
    padding: 0;
    width: 35rem;
    max-width: 90vw;
    height: auto;
}

.img_wrapper {
    position: relative;
    margin-top: 1.5rem;
    margin-bottom: 1rem;
    padding: 0;
}


.bounding_box {
    position: absolute;
    box-shadow: 0 0 0 .15rem #05d1f5 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    opacity: 0.3;
    cursor: pointer;
    transition: all .2s ease-out;
}

.bounding_box:hover {
    opacity: 1;
}

.preloader_wrap {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

    .lds_spinner {
        color: official;
        position: absolute;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 80px;
        height: 80px;
        opacity: .5;
        padding: 0;
      }
      .lds_spinner div {
        transform-origin: 40px 40px;
        animation: lds_spinner 1.2s linear infinite;
      }
      .lds_spinner div:after {
        content: " ";
        display: block;
        position: absolute;
        top: 3px;
        left: 37px;
        width: 6px;
        height: 18px;
        border-radius: 20%;
        background: #fff;
      }
      .lds_spinner div:nth-child(1) {
        transform: rotate(0deg);
        animation-delay: -1.1s;
      }
      .lds_spinner div:nth-child(2) {
        transform: rotate(30deg);
        animation-delay: -1s;
      }
      .lds_spinner div:nth-child(3) {
        transform: rotate(60deg);
        animation-delay: -0.9s;
      }
      .lds_spinner div:nth-child(4) {
        transform: rotate(90deg);
        animation-delay: -0.8s;
      }
      .lds_spinner div:nth-child(5) {
        transform: rotate(120deg);
        animation-delay: -0.7s;
      }
      .lds_spinner div:nth-child(6) {
        transform: rotate(150deg);
        animation-delay: -0.6s;
      }
      .lds_spinner div:nth-child(7) {
        transform: rotate(180deg);
        animation-delay: -0.5s;
      }
      .lds_spinner div:nth-child(8) {
        transform: rotate(210deg);
        animation-delay: -0.4s;
      }
      .lds_spinner div:nth-child(9) {
        transform: rotate(240deg);
        animation-delay: -0.3s;
      }
      .lds_spinner div:nth-child(10) {
        transform: rotate(270deg);
        animation-delay: -0.2s;
      }
      .lds_spinner div:nth-child(11) {
        transform: rotate(300deg);
        animation-delay: -0.1s;
      }
      .lds_spinner div:nth-child(12) {
        transform: rotate(330deg);
        animation-delay: 0s;
      }
      @keyframes lds_spinner {
        0% {
          opacity: 1;
        }
        100% {
          opacity: 0;
        }
      };
      

